.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #4CAF50;
    padding: 10px 50px;
    color: white;
  }
  
  .navbar-logo {
    font-size: 1.5em;
    font-weight: bold;
    color: white;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
    padding: 0;
  }
  
  .navbar-links li {
    display: inline;
  }
  
  .navbar-links a {
    color: white;
    text-decoration: none;
    font-size: 1em;
    transition: color 0.3s ease;
  }
  
  .navbar-links a:hover {
    color: #e0e0e0;
  }