body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
  }
  
  .homepage {
    text-align: center;
    padding: 50px 20px;
  }
  
  .homepage-header {
    background-color: #4CAF50;
    color: white;
    padding: 20px;
    border-radius: 10px;
    margin-bottom: 30px;
  }
  
  .homepage-header h1 {
    margin: 0;
    font-size: 2.5em;
  }
  
  .homepage-header p {
    font-size: 1.2em;
    margin-bottom: 24px;
  }
  
  .cta-button {
    background-color: #ff5722;
    color: white;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1em;
    transition: background-color 0.3s ease;
    margin-bottom: 24px;
  }
  
  .cta-button:hover {
    background-color: #e64a19;
  }
  
  .features {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .feature {
    background-color: white;
    padding: 20px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 300px;
  }
  
  .feature h2 {
    color: #4CAF50;
  }
  
  .feature p {
    color: #555;
  }